'use client';

import React, { useState, useRef, useEffect } from 'react';
import Icon from '@mdi/react';
import { 
  mdiArrowLeft, 
  mdiChevronDown,
  mdiPlus,
  mdiMinus,
  mdiDelete
} from '@mdi/js';
import { colors } from '@/lib/design-tokens';
import clsx from 'clsx';
import { MenuItemDetails } from './MenuItemDetails';

interface MenuWithSections {
  name: string;
  entryPoint: string;
  sections?: MenuSection[];
}

interface MobileMenuOrderingProps {
  onBack: () => void;
  onViewCart?: () => void;
  menuName?: string;
  menus?: MenuWithSections[];
  cart?: Record<string, number>;
  onUpdateQuantity?: (item: SectionItem, quantity: number) => void;
}

interface SectionItem {
  id: string;
  name: string;
  image: string;
}

interface MenuSection {
  id: string;
  title: string;
  items: SectionItem[];
}

// Button component
interface ButtonProps {
  children?: React.ReactNode;
  onClick?: () => void;
  variant?: 'primary' | 'outlined' | 'text' | 'icon';
  icon?: React.ReactNode;
  iconPosition?: 'left' | 'right';
  className?: string;
  disabled?: boolean;
}

const Button: React.FC<ButtonProps> = ({ 
  children, 
  onClick, 
  variant = 'primary',
  icon,
  iconPosition = 'left',
  className = '',
  disabled = false
}) => {
  const baseClasses = "inline-flex items-center justify-center font-roboto font-medium transition-all duration-200 outline-none";
  
  const variantClasses = {
    primary: "bg-black text-white hover:opacity-90 h-12 px-4 rounded",
    outlined: "bg-white border border-gray-600 text-gray-900 hover:bg-gray-50 h-12 px-2 rounded",
    text: "bg-transparent text-gray-700 hover:text-black h-10 px-4",
    icon: "bg-transparent text-gray-700 hover:text-black w-10 h-10 rounded-full hover:bg-gray-100"
  };
  
  const iconClasses = variant === 'icon' ? "w-6 h-6" : "w-4 h-4";
  const spacing = variant === 'icon' ? "" : (iconPosition === 'left' ? "mr-2" : "ml-2");
  
  return (
    <button 
      onClick={onClick}
      disabled={disabled}
      className={clsx(baseClasses, variantClasses[variant], className, {
        'opacity-50 cursor-not-allowed': disabled
      })}
    >
      {icon && iconPosition === 'left' && (
        <span className={clsx(iconClasses, spacing)}>{icon}</span>
      )}
      {variant !== 'icon' && children}
      {icon && iconPosition === 'right' && (
        <span className={clsx(iconClasses, spacing)}>{icon}</span>
      )}
    </button>
  );
};

// Select component
interface SelectProps {
  options: Array<{ label: string; value: string }>;
  value?: string;
  onChange?: (event: React.ChangeEvent<HTMLSelectElement>) => void;
  placeholder?: string;
}

const Select: React.FC<SelectProps> = ({ 
  options, 
  value, 
  onChange, 
  placeholder 
}) => {
  return (
    <div className="relative">
      <select
        value={value}
        onChange={onChange}
        className="w-full h-12 px-3 pr-10 bg-white border border-gray-600 rounded font-roboto text-lg text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 appearance-none"
      >
        {placeholder && (
          <option value="" disabled>
            {placeholder}
          </option>
        )}
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
      <div className="absolute right-3 top-1/2 -translate-y-1/2 pointer-events-none">
        <Icon path={mdiChevronDown} size={1} color={colors.black3} />
      </div>
    </div>
  );
};

// Tab component
interface TabProps {
  children: React.ReactNode;
  active?: boolean;
  onClick?: () => void;
}

const Tab: React.FC<TabProps> = ({ children, active = false, onClick }) => (
  <button
    onClick={onClick}
    className={`flex-1 px-4 py-2 font-roboto text-base font-medium transition-colors border-b-2 ${
      active 
        ? 'text-black border-black' 
        : 'text-gray-600 border-transparent hover:text-black'
    }`}
  >
    {children}
  </button>
);

// Menu Item component
interface MenuItemProps {
  item: SectionItem;
  onAddToCart?: (item: SectionItem) => void;
  quantity?: number;
  onUpdateQuantity?: (item: SectionItem, quantity: number) => void;
  onItemClick?: (item: SectionItem) => void;
}

const MenuItem: React.FC<MenuItemProps> = ({ 
  item, 
  onAddToCart, 
  quantity = 0, 
  onUpdateQuantity,
  onItemClick 
}) => {
  const handleAdd = () => {
    if (quantity === 0 && onAddToCart) {
      onAddToCart(item);
    } else if (onUpdateQuantity) {
      onUpdateQuantity(item, quantity + 1);
    }
  };

  const handleRemove = () => {
    if (onUpdateQuantity && quantity > 0) {
      onUpdateQuantity(item, quantity - 1);
    }
  };

  // Generate a consistent mock price based on item name hash
  const mockPrice = Math.floor((item.name.length * 7 + item.id.length * 3) % 30) + 15;

  return (
    <div className="flex items-center gap-4 p-3 border-b border-neutral-200 last:border-b-0">
      {/* Item Image */}
      <img 
        src={item.image} 
        alt={item.name}
        className="w-16 h-16 rounded-lg object-cover shrink-0 cursor-pointer"
        onClick={() => onItemClick?.(item)}
      />
      
      {/* Item Info */}
      <div 
        className="flex-1 min-w-0 cursor-pointer" 
        onClick={() => onItemClick?.(item)}
      >
        <h4 className="font-roboto text-sm font-medium text-black mb-0.5 truncate">
          {item.name}
        </h4>
        <p className="font-roboto text-sm font-normal text-black">
          ${mockPrice}
        </p>
      </div>
      
      {/* Add/Counter */}
      <div className="shrink-0">
        {quantity === 0 ? (
          // Default state: Just a "+" button
          <button
            onClick={handleAdd}
            className="bg-black text-white w-9 h-9 flex items-center justify-center rounded hover:opacity-90 transition-opacity"
          >
            <Icon path={mdiPlus} size={0.7} color="white" />
          </button>
        ) : (
          // Counter state: trash/minus + number + plus
          <div className="flex items-center gap-2 bg-transparent rounded overflow-hidden">
            <button
              onClick={handleRemove}
              className="bg-black text-white w-9 h-9 flex items-center justify-center rounded hover:opacity-90 transition-opacity"
            >
              <Icon 
                path={quantity === 1 ? mdiDelete : mdiMinus} 
                size={0.7} 
                color="white" 
              />
            </button>
            <span className="font-roboto font-bold text-base text-black text-center w-8 tracking-wide">
              {quantity}
            </span>
            <button
              onClick={handleAdd}
              className="bg-black text-white w-9 h-9 flex items-center justify-center rounded hover:opacity-90 transition-opacity"
            >
              <Icon path={mdiPlus} size={0.7} color="white" />
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export const MobileMenuOrdering: React.FC<MobileMenuOrderingProps> = ({ 
  onBack,
  onViewCart,
  menuName = "Breakfast menu",
  menus = [],
  cart = {},
  onUpdateQuantity
}) => {
  const [selectedMenuName, setSelectedMenuName] = useState(menuName);
  const [activeSection, setActiveSection] = useState('');
  const [selectedItem, setSelectedItem] = useState<SectionItem | null>(null);
  const [isItemDetailsOpen, setIsItemDetailsOpen] = useState(false);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const sectionRefs = useRef<Record<string, HTMLDivElement | null>>({});
  
  // Convert menus to select options format
  const menuOptions = menus.map(menu => ({
    label: menu.name,
    value: menu.name
  }));

  // Get current selected menu and its sections
  const currentMenu = menus.find(menu => menu.name === selectedMenuName);
  const menuSections = currentMenu?.sections || [];

  // Set initial active section
  useEffect(() => {
    if (menuSections.length > 0 && !activeSection) {
      setActiveSection(menuSections[0].id);
    }
  }, [menuSections, activeSection]);

  // Intersection Observer for auto-updating active tab
  useEffect(() => {
    if (!scrollContainerRef.current || menuSections.length === 0) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const sectionId = entry.target.getAttribute('data-section-id');
            if (sectionId) {
              setActiveSection(sectionId);
            }
          }
        });
      },
      {
        root: scrollContainerRef.current,
        rootMargin: '-50px 0px -50% 0px',
        threshold: 0
      }
    );

    // Observe all section elements
    Object.values(sectionRefs.current).forEach((element) => {
      if (element) {
        observer.observe(element);
      }
    });

    return () => {
      observer.disconnect();
    };
  }, [menuSections]);

  const handleMenuSelect = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const newMenuName = event.target.value;
    setSelectedMenuName(newMenuName);
    
    // Reset active section to first section of the new menu
    const newMenu = menus.find(menu => menu.name === newMenuName);
    const newMenuSections = newMenu?.sections || [];
    setActiveSection(newMenuSections.length > 0 ? newMenuSections[0].id : '');
  };

  const scrollToSection = (sectionId: string) => {
    const sectionElement = sectionRefs.current[sectionId];
    if (sectionElement && scrollContainerRef.current) {
      const container = scrollContainerRef.current;
      const containerRect = container.getBoundingClientRect();
      const sectionRect = sectionElement.getBoundingClientRect();
      
      const scrollTop = container.scrollTop + (sectionRect.top - containerRect.top) - 20; // 20px offset for better positioning
      
      container.scrollTo({
        top: scrollTop,
        behavior: 'smooth'
      });
    }
    setActiveSection(sectionId);
  };

  const handleAddToCart = (item: SectionItem) => {
    if (onUpdateQuantity) {
      onUpdateQuantity(item, 1);
    }
  };

  const handleUpdateQuantity = (item: SectionItem, quantity: number) => {
    if (onUpdateQuantity) {
      onUpdateQuantity(item, quantity);
    }
  };

  const handleItemClick = (item: SectionItem) => {
    setSelectedItem(item);
    setIsItemDetailsOpen(true);
  };

  const handleItemDetailsClose = () => {
    setIsItemDetailsOpen(false);
    setSelectedItem(null);
  };

  const handleAddToCartFromDetails = (item: SectionItem, quantity: number, specialRequests: string) => {
    if (onUpdateQuantity) {
      onUpdateQuantity(item, (cart[item.id] || 0) + quantity);
    }
    // You could also store special requests in a separate state if needed
    console.log('Special requests:', specialRequests);
  };

  const totalCartItems = Object.values(cart).reduce((sum, qty) => sum + qty, 0);

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100 p-8">
      {/* Back to Mobile Landing Button */}
      <div className="fixed top-8 left-8 z-50">
        <Button
          onClick={onBack}
          variant="outlined"
          icon={<Icon path={mdiArrowLeft} size={0.8} />}
          iconPosition="left"
        >
          Back to Mobile Landing
        </Button>
      </div>

      {/* Mobile Frame */}
      <div 
        className="relative bg-white overflow-hidden shadow-xl"
        style={{
          width: '430px',
          height: '932px',
          borderRadius: '44px',
          boxShadow: '0px 8px 16px 0px rgba(0,0,0,0.16)'
        }}
      >
        {/* Header */}
        <div className="flex items-center justify-between px-4 py-4 bg-white">
          <Button 
            onClick={onBack}
            variant="icon"
            icon={<Icon path={mdiArrowLeft} size={1} />}
          />
          
          <h1 className="font-roboto text-2xl font-semibold text-black text-center">
            In-room dining
          </h1>
          
          <div className="w-10 h-10 opacity-0" />
        </div>

        {/* Menu Dropdown */}
        <div className="px-4 py-3">
          <Select
            options={menuOptions}
            value={selectedMenuName}
            onChange={handleMenuSelect}
            placeholder="Select a menu"
          />
        </div>

        {/* Menu Title */}
        <div className="px-4 py-3">
          <h2 className="font-roboto text-[32px] font-medium text-black leading-[48px]">
            {selectedMenuName}
          </h2>
        </div>

        {/* Section Tabs */}
        <div className="px-4 border-b border-neutral-200">
          <div className="flex">
            {menuSections.map((section) => (
              <Tab
                key={section.id}
                active={activeSection === section.id}
                onClick={() => scrollToSection(section.id)}
              >
                {section.title}
              </Tab>
            ))}
          </div>
        </div>

        {/* Scrollable Content */}
        <div 
          ref={scrollContainerRef}
          className="flex-1 overflow-y-auto px-4 py-6"
          style={{ height: 'calc(100% - 240px)' }}
        >
          {menuSections.map((section) => (
            <div 
              key={section.id}
              ref={(el) => { sectionRefs.current[section.id] = el; }}
              data-section-id={section.id}
              className="mb-6 last:mb-0"
            >
              {/* Section Header */}
              <h3 className="font-roboto text-xl font-medium text-black mb-3">
                {section.title}
              </h3>
              
              {/* Section Items */}
              <div className="border border-neutral-200 rounded-lg overflow-hidden">
                {section.items.length === 0 ? (
                  <div className="p-8 text-center">
                    <p className="text-gray-500 font-roboto text-sm">
                      No items in this section yet
                    </p>
                  </div>
                ) : (
                  section.items.map((item) => (
                    <MenuItem
                      key={item.id}
                      item={item}
                      quantity={cart[item.id] || 0}
                      onAddToCart={handleAddToCart}
                      onUpdateQuantity={handleUpdateQuantity}
                      onItemClick={handleItemClick}
                    />
                  ))
                )}
              </div>
            </div>
          ))}
        </div>

        {/* Sticky Footer - Cart Button */}
        {totalCartItems > 0 && (
          <div className="absolute bottom-0 left-0 right-0 bg-white border-t border-neutral-200 p-4">
            <Button 
              className="w-full" 
              onClick={() => {
                console.log(' View cart clicked!');
                console.log(' Total cart items:', totalCartItems);
                console.log(' Cart state:', cart);
                onViewCart?.();
              }}
            >
              View cart ({totalCartItems})
            </Button>
          </div>
        )}

        {/* Menu Item Details Modal */}
        <MenuItemDetails
          item={selectedItem}
          isOpen={isItemDetailsOpen}
          onClose={handleItemDetailsClose}
          onAddToCart={handleAddToCartFromDetails}
          initialQuantity={selectedItem ? cart[selectedItem.id] || 1 : 1}
        />
      </div>
    </div>
  );
};