'use client';

import React, { useState } from 'react';
import { MenuManagementPage } from './MenuManagementPage';
import { EditMenuPage } from './EditMenuPage';
import { ItemLibraryPage } from './ItemLibraryPage';
import { EditItemPage } from './EditItemPage';
import { MobileGuestExperience } from './MobileGuestExperience';
import { MobileMenuOrdering } from './MobileMenuOrdering';
import { OrderSummary } from './OrderSummary';
import { OrderSubmissionLoading } from './OrderSubmissionLoading';
import { OrderConfirmation } from './OrderConfirmation';
import { EditSectionPage } from './EditSectionPage';
import { MenuAvailabilityPage } from './MenuAvailabilityPage';
import { Toast } from './Toast';

type Page = 'menu-management' | 'edit-menu' | 'item-library' | 'edit-item' | 'edit-section' | 'menu-availability' | 'mobile-preview' | 'mobile-menu-ordering' | 'order-summary' | 'order-loading' | 'order-confirmation';

interface MenuItem {
  name: string;
  entryPoint: string;
  isNew?: boolean;
}

interface FoodItem {
  id: string;
  name: string;
  description: string;
  price: number;
  image?: string;
}

interface SectionItem {
  id: string;
  name: string;
  image: string;
}

interface MenuSection {
  id: string;
  title: string;
  items: SectionItem[];
}

interface MenuWithSections extends MenuItem {
  sections?: MenuSection[];
}

interface AppState {
  currentPage: Page;
  editingMenu?: MenuWithSections;
  previewingMenu?: MenuWithSections;
  editingItem?: FoodItem;
  editingSection?: MenuSection;
  menus: MenuWithSections[];
  cart: Record<string, number>;
  toast: {
    isVisible: boolean;
    message: string;
    type: 'success' | 'error' | 'warning';
  };
}

export const AppRouter: React.FC = () => {
  // Initialize menus with actual section data
  const initialMenus: MenuWithSections[] = [
    { 
      name: 'Breakfast menu', 
      entryPoint: 'In-room dining',
      sections: [
        {
          id: 'mains',
          title: 'Mains',
          items: [
            { id: '3', name: 'Croissant', image: 'https://images.unsplash.com/photo-1555507036-ab794f4eeecb?w=40&h=40&fit=crop&crop=center' },
            { id: '5', name: 'Frittata', image: 'https://images.unsplash.com/photo-1506084868230-bb9d95c24759?w=40&h=40&fit=crop&crop=center' }
          ]
        }
      ]
    },
    { 
      name: 'Lunch menu', 
      entryPoint: 'In-room dining',
      sections: [
        {
          id: 'cold',
          title: 'Cold',
          items: [
            { id: '6', name: 'Salad', image: 'https://images.unsplash.com/photo-1512621776951-a57141f2eefd?w=40&h=40&fit=crop&crop=center' }
          ]
        },
        {
          id: 'mains',
          title: 'Mains',
          items: [
            { id: '1', name: 'Burrito', image: 'https://images.unsplash.com/photo-1566740933430-b5e70b06d2d5?w=40&h=40&fit=crop&crop=center' },
            { id: '3', name: 'Croissant', image: 'https://images.unsplash.com/photo-1555507036-ab794f4eeecb?w=40&h=40&fit=crop&crop=center' },
            { id: '4', name: 'Dumplings', image: 'https://images.unsplash.com/photo-1496116218417-1a781b1c416c?w=40&h=40&fit=crop&crop=center' },
            { id: '9', name: 'Pasta', image: 'https://images.unsplash.com/photo-1551183053-bf91a1d81141?w=40&h=40&fit=crop&crop=center' },
            { id: '10', name: 'Pizza', image: 'https://images.unsplash.com/photo-1565299624946-b28f40a0ca4b?w=40&h=40&fit=crop&crop=center' }
          ]
        },
        {
          id: 'sides',
          title: 'Sides',
          items: [
            { id: '11', name: 'Taco', image: 'https://images.unsplash.com/photo-1551504734-5ee1c4a1479b?w=40&h=40&fit=crop&crop=center' }
          ]
        }
      ]
    },
    { name: 'Dinner menu', entryPoint: 'In-room dining', sections: [] },
    { name: 'Happy hour menu', entryPoint: 'In-room dining', sections: [] },
    { name: 'Dessert menu', entryPoint: 'In-room dining', sections: [] },
  ];

  const [appState, setAppState] = useState<AppState>({
    currentPage: 'menu-management',
    menus: initialMenus,
    cart: {},
    toast: {
      isVisible: false,
      message: '',
      type: 'success'
    }
  });

  const navigateToEditMenu = (menuName: string, entryPoint: string) => {
    const menu = appState.menus.find(m => m.name === menuName);
    setAppState(prev => ({
      ...prev,
      currentPage: 'edit-menu',
      editingMenu: menu || { name: menuName, entryPoint }
    }));
  };

  const navigateToMenuManagement = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'menu-management'
    }));
  };

  const navigateToMobilePreview = (menuName: string, entryPoint: string) => {
    const menu = appState.menus.find(m => m.name === menuName);
    setAppState(prev => ({
      ...prev,
      currentPage: 'mobile-preview',
      previewingMenu: menu || { name: menuName, entryPoint }
    }));
  };

  const navigateToMobileMenuOrdering = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'mobile-menu-ordering'
    }));
  };

  const navigateBackToMobilePreview = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'mobile-preview'
    }));
  };

  const navigateToOrderSummary = () => {
    console.log(' Navigating to order summary...');
    console.log(' Current cart state:', appState.cart);
    setAppState(prev => ({
      ...prev,
      currentPage: 'order-summary'
    }));
  };

  const navigateBackToMobileMenuOrdering = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'mobile-menu-ordering'
    }));
  };

  const navigateToOrderLoading = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'order-loading'
    }));
    
    // After 3 seconds, navigate to confirmation
    setTimeout(() => {
      setAppState(prev => ({
        ...prev,
        currentPage: 'order-confirmation'
      }));
    }, 3000);
  };

  const navigateToOrderConfirmation = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'order-confirmation'
    }));
  };

  const closeOrderFlow = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'mobile-menu-ordering',
      cart: {} // Clear the cart after order completion
    }));
  };

  const handleUpdateQuantity = (item: SectionItem, quantity: number) => {
    console.log(' Updating cart quantity:', item.name, 'to', quantity);
    setAppState(prev => {
      if (quantity <= 0) {
        const { [item.id]: removed, ...rest } = prev.cart;
        console.log(' Removing item from cart:', item.name);
        console.log(' New cart:', rest);
        return { ...prev, cart: rest };
      }
      const newCart = { ...prev.cart, [item.id]: quantity };
      console.log(' New cart state:', newCart);
      return { 
        ...prev, 
        cart: newCart
      };
    });
  };

  // Get all cart items from menus
  const getAllCartItems = (): SectionItem[] => {
    const items: SectionItem[] = [];
    appState.menus.forEach(menu => {
      menu.sections?.forEach(section => {
        section.items.forEach(item => {
          if (appState.cart[item.id]) {
            items.push(item);
          }
        });
      });
    });
    return items;
  };

  const navigateToItemLibrary = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'item-library'
    }));
  };

  const navigateToEditItem = (itemId: string) => {
    // In a real app, you'd fetch the item data from an API
    // For now, we'll create a mock item based on the sample data
    const sampleItems: FoodItem[] = [
      {
        id: '1',
        name: 'Burrito',
        description: 'Delicious burrito with fresh ingredients',
        price: 17.00,
        image: 'https://images.unsplash.com/photo-1566740933430-b5e70b06d2d5?w=400&h=300&fit=crop&crop=center'
      },
      {
        id: '2',
        name: 'Brownie',
        description: 'Rich chocolate brownie with fudge',
        price: 5.00,
        image: 'https://images.unsplash.com/photo-1606890737304-57a1ca8a5b62?w=400&h=300&fit=crop&crop=center'
      },
      {
        id: '3',
        name: 'Croissant',
        description: 'Flaky, buttery croissant fresh from the oven',
        price: 8.00,
        image: 'https://images.unsplash.com/photo-1555507036-ab794f4eeecb?w=400&h=300&fit=crop&crop=center'
      }
    ];

    const item = sampleItems.find(i => i.id === itemId) || sampleItems[0];
    
    setAppState(prev => ({
      ...prev,
      currentPage: 'edit-item',
      editingItem: item
    }));
  };

  const saveItem = (item: FoodItem) => {
    // In a real app, you'd save to an API
    setAppState(prev => ({
      ...prev,
      currentPage: 'item-library',
      editingItem: undefined
    }));
    showToast('Item saved successfully', 'success');
  };

  const navigateToEditSection = (sectionId: string) => {
    // Find the actual section from the current menu
    const currentMenu = appState.editingMenu;
    const section = currentMenu?.sections?.find(s => s.id === sectionId);
    
    if (section) {
      setAppState(prev => ({
        ...prev,
        currentPage: 'edit-section',
        editingSection: section
      }));
    } else {
      // Create new section if not found
      const newSection: MenuSection = {
        id: sectionId,
        title: sectionId.charAt(0).toUpperCase() + sectionId.slice(1),
        items: []
      };
      setAppState(prev => ({
        ...prev,
        currentPage: 'edit-section',
        editingSection: newSection
      }));
    }
  };

  const navigateBackToEditMenu = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'edit-menu'
    }));
  };

  const saveSection = (section: MenuSection) => {
    // Update the section in the current menu
    setAppState(prev => {
      if (!prev.editingMenu) return prev;

      const updatedMenus = prev.menus.map(menu => {
        if (menu.name === prev.editingMenu?.name) {
          const updatedSections = menu.sections ? 
            menu.sections.map(s => s.id === section.id ? section : s) :
            [section];
          
          // If section doesn't exist, add it
          if (!menu.sections?.some(s => s.id === section.id)) {
            updatedSections.push(section);
          }

          return {
            ...menu,
            sections: updatedSections
          };
        }
        return menu;
      });

      return {
        ...prev,
        currentPage: 'edit-menu',
        editingSection: undefined,
        menus: updatedMenus,
        editingMenu: updatedMenus.find(m => m.name === prev.editingMenu?.name)
      };
    });
    showToast('Section saved successfully', 'success');
  };

  const navigateToMenuAvailability = () => {
    setAppState(prev => ({
      ...prev,
      currentPage: 'menu-availability'
    }));
  };

  const saveMenuAvailability = (availability: any[]) => {
    // In a real app, you'd save to an API
    setAppState(prev => ({
      ...prev,
      currentPage: 'edit-menu'
    }));
    showToast('Menu availability saved successfully', 'success');
  };

  const createNewMenu = (menuName: string) => {
    const newMenu: MenuItem = {
      name: menuName,
      entryPoint: 'In-room dining',
      isNew: true
    };

    setAppState(prev => ({
      ...prev,
      menus: [...prev.menus, newMenu],
      currentPage: 'edit-menu',
      editingMenu: newMenu
    }));
  };

  const saveMenu = (menuName: string, isNew: boolean) => {
    if (!menuName.trim()) {
      showToast('Menu name is required', 'error');
      return false;
    }

    if (isNew) {
      setAppState(prev => ({
        ...prev,
        menus: prev.menus.map(menu =>
          menu.isNew && menu.name === prev.editingMenu?.name
            ? { ...menu, name: menuName, isNew: false }
            : menu
        ),
        editingMenu: prev.editingMenu ? { ...prev.editingMenu, name: menuName, isNew: false } : undefined
      }));
      showToast('Menu created successfully', 'success');
    } else {
      showToast('Menu saved successfully', 'success');
    }
    return true;
  };

  const deleteMenu = (menuName: string) => {
    setAppState(prev => ({
      ...prev,
      menus: prev.menus.filter(menu => menu.name !== menuName)
    }));
    showToast('Menu deleted successfully', 'success');
  };

  const showToast = (message: string, type: 'success' | 'error' | 'warning') => {
    setAppState(prev => ({
      ...prev,
      toast: {
        isVisible: true,
        message,
        type
      }
    }));
  };

  const hideToast = () => {
    setAppState(prev => ({
      ...prev,
      toast: {
        ...prev.toast,
        isVisible: false
      }
    }));
  };

  console.log(' Current page:', appState.currentPage);
  console.log(' App state:', { 
    currentPage: appState.currentPage, 
    cartSize: Object.keys(appState.cart).length,
    cart: appState.cart 
  });
  
  switch (appState.currentPage) {
    case 'menu-availability':
      return (
        <>
          <MenuAvailabilityPage
            menuName={appState.editingMenu?.name}
            onBack={navigateBackToEditMenu}
            onSave={saveMenuAvailability}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'edit-section':
      return (
        <>
          <EditSectionPage
            section={appState.editingSection}
            onBack={navigateBackToEditMenu}
            onSave={saveSection}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'edit-item':
      return (
        <>
          <EditItemPage
            item={appState.editingItem}
            onBack={navigateToItemLibrary}
            onSave={saveItem}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'item-library':
      return (
        <>
          <ItemLibraryPage
            onBack={navigateToMenuManagement}
            onTabChange={(tab) => {
              if (tab === 'menus') navigateToMenuManagement();
              if (tab === 'item-library') navigateToItemLibrary();
              // settings tab will be added later
            }}
            onEditItem={navigateToEditItem}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'mobile-menu-ordering':
      return (
        <>
          <MobileMenuOrdering
            menuName={appState.previewingMenu?.name}
            menus={appState.menus}
            cart={appState.cart}
            onBack={navigateBackToMobilePreview}
            onViewCart={navigateToOrderSummary}
            onUpdateQuantity={handleUpdateQuantity}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'order-summary':
      return (
        <>
          <OrderSummary
            cart={appState.cart}
            cartItems={getAllCartItems()}
            onBack={navigateBackToMobileMenuOrdering}
            onAddMoreItems={navigateBackToMobileMenuOrdering}
            onUpdateQuantity={handleUpdateQuantity}
            onSubmitOrder={navigateToOrderLoading}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'mobile-preview':
      return (
        <>
          <MobileGuestExperience
            menuName={appState.previewingMenu?.name}
            onBack={navigateToMenuManagement}
            onOrderClick={navigateToMobileMenuOrdering}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'edit-menu':
      return (
        <>
          <EditMenuPage
            menuName={appState.editingMenu?.name}
            isNewMenu={appState.editingMenu?.isNew}
            sections={appState.editingMenu?.sections}
            onBack={navigateToMenuManagement}
            onSave={saveMenu}
            onEditSection={navigateToEditSection}
            onAddAvailabilityHours={navigateToMenuAvailability}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
    case 'menu-management':
    default:
      return (
        <>
          <MenuManagementPage
            menus={appState.menus}
            onEditMenu={navigateToEditMenu}
            onCreateMenu={createNewMenu}
            onDeleteMenu={deleteMenu}
            onPreviewMenu={navigateToMobilePreview}
            onItemLibraryClick={navigateToItemLibrary}
          />
          <Toast
            message={appState.toast.message}
            type={appState.toast.type}
            isVisible={appState.toast.isVisible}
            onClose={hideToast}
          />
        </>
      );
  }
};